.Process {
    padding: 60px 16px;
    background: #EBF0EB;
}

.Wrapper {
    max-width: 1200px;
    margin: auto;
}

    .Header span,
    .About span {
        display: inline-block;
        margin-bottom: 10px;
        font-weight: 600;
        font-size: 20px;
        color: #FF0000;
    }
    .Header span::before,
    .About span::before {
        content: '';
        display: inline-block;
        width: 50px;
        height: 2px;
        margin-right: 20px;
        vertical-align: middle;
        background: #FF0000;
    }
    .Header h2,
    .About h2 {
        font-weight: 600;
        font-size: 42px;
        color: #222222;
    }
    .Header p {
        font-size: 22px;
        color: #848484;
    }
    .Header a {
        font-family: 'Libre Franklin', sans-serif;
        font-weight: 500;
        text-decoration: none;
        color: #848484;
        transition: all 0.3s ease;
    }
    .Header a:hover {
        color: #FF0000;
    }

.Phase {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2vw;
    margin: 40px 0;
}
    .Phase div div {
        font-family: 'Libre Franklin', sans-serif;
        display: inline-block;
        width: 60px;
        height: 60px;
        line-height: 60px;
        vertical-align: middle;
        text-align: center;
        font-weight: 500;
        font-size: 22px;
        border-radius: 50%;
        background: #fff;
        color: #FF0000;
    }
    .Phase h3 {
        min-height: 51px;
        margin: 20px 0;
        font-weight: 600;
        font-size: 22px;
    }
    .Phase p {
        text-align: justify;
        color: #848484;
    }

/* ----------------------------------About----------------------------------- */
.About {
    padding: 60px 16px;
}
